import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { Router } from '@angular/router';
import { AuthenticationService } from '../services/authentication.service';


@Component({
  selector: 'app-users',
  templateUrl: './users.page.html',
  styleUrls: ['./users.page.scss'],
})
export class UsersPage implements OnInit {

  users:any;
  regNo:any;
  status:any;

  constructor(private userservice : UserService,private router:Router,private authService:AuthenticationService) { }

  getAllusers(){
    this.userservice.getUsers().subscribe(data=>{
      if(data['status']==200){
        this.users=data['data'];
        console.log(this.users);
      }
      else{
        console.log(data);
      }
     });
  }
  
  ngOnInit() {
    this.getAllusers();
  }
  logOut(){
    this.authService.logout();
    this.router.navigate(['/login']);
  }
  clickDelete(user){
    this.regNo=user.regno
    console.log(this.regNo);
    this.userservice.deleteUser(this.regNo).subscribe(data=>{
      this.getAllusers();
      console.log(data);
    })
  }

  confirmUser(user){
    this.regNo=user.regno;
    this.status="active";
    this.userservice.updateUser(this.regNo,this.status).subscribe(data=>{
      this.getAllusers();
    })
  }
}
